import React, {
  useContext,
  useCallback,
  useMemo,
  useState,
  useEffect,
} from 'react'
import styled from 'styled-components'
import { observer } from 'mobx-react-lite'
import { Form } from 'reactstrap'
import moment from 'moment'
import sortBy from 'lodash/sortBy'

import Input from '../../shared/Input'
import Select from '../../shared/Select'
import SharedCheckbox from '../../shared/Checkbox_01'
import Handlungsbedarf from '../../shared/Handlungsbedarf'
import ifIsNumericAsNumber from '../../../src/ifIsNumericAsNumber'
import isDateField from '../../../src/isDateField'
import Zuletzt from './Zuletzt'
import storeContext from '../../../storeContext'

const Container = styled.div``
const StyledForm = styled(Form)`
  margin: 20px;
`

const Sektion = ({ activeId }) => {
  const store = useContext(storeContext)
  const {
    personen,
    abteilungen,
    sektionen,
    showDeleted,
    showMutationNoetig,
    kostenstelleWerte,
    standortWerte,
    showFilter,
    filterSektion,
    existsFilter,
    setFilter,
    updateField,
  } = store

  let sektion
  if (showFilter) {
    sektion = filterSektion
  } else {
    sektion = sektionen.find(p => p.id === activeId)
    if (!sektion) sektion = {}
  }
  const sektionId = showFilter ? '' : sektion.id

  const [errors, setErrors] = useState({})
  useEffect(() => setErrors({}), [sektion])

  const saveToDb = useCallback(
    ({ field, value }) => {
      if (!sektion && !showFilter)
        throw new Error(`Sektion with id ${activeId} not found`)
      let newValue
      if (isDateField(field)) {
        if (value) newValue = moment(value, 'DD.MM.YYYY').format('DD.MM.YYYY')
        if (newValue && newValue.includes('Invalid date')) {
          newValue = newValue.replace('Invalid date', 'Format: DD.MM.YYYY')
        }
      } else {
        newValue = ifIsNumericAsNumber(value)
      }

      if (showFilter) {
        setFilter({
          model: 'filterSektion',
          value: { ...filterSektion, ...{ [field]: newValue } },
        })
      } else {
        updateField({
          table: 'sektionen',
          parentModel: 'sektionen',
          field,
          value: newValue,
          id: sektion.id,
          setErrors,
        })
        if (field === 'mutationFrist' && newValue && !sektion.mutationNoetig) {
          // set mutationNoetig to true of not yet so
          updateField({
            table: 'sektionen',
            parentModel: 'sektionen',
            field: 'mutationNoetig',
            value: 1,
            id: sektion.id,
          })
        }
      }
    },
    [activeId, sektionen.length, filterSektion, showFilter],
  )

  // filter out options with empty values - makes no sense and errors
  const personOptions = useMemo(
    () =>
      sortBy(personen, ['name', 'vorname'])
        .filter(w => !!w.name && !!w.vorname && w.deleted === 0)
        .filter(w => !showFilter && w.id !== sektion.leiter)
        .map(w => ({
          label: `${w.name} ${w.vorname}`,
          value: w.id,
        })),
    [personen.length],
  )
  const kostenstelleOptions = useMemo(() =>
    sortBy(kostenstelleWerte, ['sort', 'value'])
      .filter(w => !!w.value)
      .map(w => ({
        label: w.value,
        value: w.value,
      })),
  )
  const standortOptions = useMemo(() =>
    sortBy(standortWerte, ['sort', 'value'])
      .filter(p => p.deleted === 0)
      .map(w => ({
        label: w.value,
        value: w.value,
      })),
  )
  const abteilungOptions = useMemo(
    () =>
      sortBy(abteilungen, ['name'])
        .filter(w => !!w.name && w.deleted === 0)
        .map(w => ({
          label: w.name,
          value: w.id,
        })),
    [abteilungen.length],
  )

  if (!showFilter && !activeId) return null

  return (
    <Container showfilter={showFilter}>
      <StyledForm>
        <Input
          key={`${sektionId}name`}
          value={sektion.name}
          field="name"
          label="Name"
          saveToDb={saveToDb}
          error={errors.name}
        />
        <Select
          key={`${sektionId}${existsFilter ? 1 : 0}abteilung`}
          value={sektion.abteilung}
          field="abteilung"
          label="Abteilung"
          options={abteilungOptions}
          saveToDb={saveToDb}
          error={errors.abteilung}
        />
        <Input
          key={`${sektionId}kurzzeichen`}
          value={sektion.kurzzeichen}
          field="kurzzeichen"
          label="Kurzzeichen"
          saveToDb={saveToDb}
          error={errors.kurzzeichen}
        />
        <Input
          key={`${sektionId}telefonNr`}
          value={sektion.telefonNr}
          field="telefonNr"
          label="Telefon"
          saveToDb={saveToDb}
          error={errors.telefonNr}
        />
        <Input
          key={`${sektionId}email`}
          value={sektion.email}
          field="email"
          label="Email"
          saveToDb={saveToDb}
          error={errors.email}
        />
        <Select
          key={`${sektionId}${existsFilter ? 1 : 0}standort`}
          value={sektion.standort}
          field="standort"
          label="Standort"
          options={standortOptions}
          saveToDb={saveToDb}
          error={errors.standort}
        />
        <Select
          key={`${sektionId}${existsFilter ? 1 : 0}leiter`}
          value={sektion.leiter}
          field="leiter"
          label="Leiter"
          options={personOptions}
          saveToDb={saveToDb}
          error={errors.leiter}
        />
        <Select
          key={`${sektionId}${existsFilter ? 1 : 0}kostenstelle`}
          value={sektion.kostenstelle}
          field="kostenstelle"
          label="Kostenstelle"
          options={kostenstelleOptions}
          saveToDb={saveToDb}
          error={errors.kostenstelle}
        />
        {showMutationNoetig && (
          <Handlungsbedarf
            key={`${sektionId}mutationHandlungsbedarf`}
            mutationFristValue={sektion.mutationFrist}
            mutationNoetigValue={sektion.mutationNoetig}
            label="Handlungs&shy;bedarf"
            saveToDb={saveToDb}
            errorMutationNoetig={errors.mutationNoetig}
            errorMutationFrist={errors.mutationFrist}
          />
        )}
        {showDeleted && (
          <SharedCheckbox
            key={`${sektionId}deleted`}
            value={sektion.deleted}
            field="deleted"
            label="Gelöscht"
            saveToDb={saveToDb}
            error={errors.deleted}
          />
        )}
        {!showFilter && <Zuletzt />}
      </StyledForm>
    </Container>
  )
}

export default observer(Sektion)
